*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #555;
}
.quiz-app{
    width: 600px;
    height: auto;
    background-color: #fff;
    text-align: center;
    align-items: center;
    border-radius: 20px;
}
.ques-heading{
    font-weight: bold;
    margin: 10px 20px;
    padding: 5px;
    color: #4898db;
}
.option{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    gap: 10px;
}
.option button{
    font-size: 16px;
    padding: 10px 15px;
    border-radius: 30px;
    background-color: #4898db;
    cursor: pointer;
}
.timer{
    color: #ccc;
    font-size: 16px;
    margin-top: 15px;
    padding: 10px;
}
.timer span{
    font-weight: 600;
    color: black;
}
.result{
    text-align: center;
    align-items: center;
    padding: 15px 25px;
}
.score{
    margin-bottom: 10px;
}
.restart-btn{
    background-color: tomato;
    padding: 10px;
    border: none;
    border-radius: 20px;
    color: #fff;
}